<template>
  <div class="receive_box">
    <div class="receive_nav">
      <div class="receive_nav_btn" @click="goBack">
        <i v-if="isShow" class="el-icon-back"></i>
      </div>
      <div class="receive_nav_title">{{$t('Red Packet')}}</div>
      <div class="receive_nav_text">
        <router-link to="/">
          <i class="el-icon-s-home"></i>
        </router-link>
      </div>
    </div>
    <red-top :red-obj="redObj"></red-top>
    <red-data :red-obj="redObj"></red-data>
    <div class="receive_input">
      <div class="account_input">
        <input type="text" v-model="accountName">
      </div>
      <div class="input_text">
        <span>{{$t('NoAccount')}}</span>
        <span style="color: #fff7c1;" @click="goAccount">{{$t('Create Account')}}</span>
      </div>
      <slider @validUser="getEnvelope"></slider>
    </div>
    <loading :loading-status="loading"></loading>
  </div>
</template>

<script>
export default {
  components: {
    loading: resolve => {
      require(["@/components/loading.vue"], resolve);
    },
    redTop: resolve => {
      require(["@/components/redTop.vue"], resolve);
    },
    redData: resolve => {
      require(["@/components/redData.vue"], resolve);
    },
    slider: resolve => {
      require(["@/components/slider.vue"], resolve);
    }
  },
  data() {
    return {
      loading: false,
      redObj: null,
      accountName: null,
      isShow: true
    };
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (from.name == null) {
        vm.isShow = false;
      } else {
        vm.isShow = true;
      }
    });
  },

  async created() {
    if (this.$route.query.lang == "zh") {
      localStorage.setItem("Lang", "zh");
      this.$i18n.locale = "zh";
    } else if (this.$route.query.lang == "en") {
      localStorage.setItem("Lang", "en");
      this.$i18n.locale = "en";
    }
    //this.loading = true;
    //get id from string 1543940193333
    let id = this.$route.query.id; //location.href.split("=")[1];
    console.log(id);
    try {
      this.redObj = await this.chainApi.getRecord(id);
      console.log(this.accountName);
      if (this.redObj.rest_number == 0) {
        this.$message.error(this.$t("RedOverMessage"));
      }
      this.accountName = (await this.chainApi.connectIdentity()).name;
      console.log(this.accountName);
    } catch (err) {
      this.$message.error(this.$t(err));
    }
  },

  methods: {
    goBack() {
      window.history.go(-1);
    },
    goAccount() {
      this.$router.push({
        path:
          "account?id=" +
          this.$route.query.id +
          "&f=" +
          this.$route.query.f +
          "&l=" +
          this.$route.query.l
      });
    },
    getEnvelope() {
      //input user
      let account = this.accountName; //"qsx.io";
      if (account == "" || account == null) {
        this.$message.error(this.$t("AccountNull"));
      } else {
        let id = this.redObj.envelope_id;
        if (localStorage[id]) {
          this.$message.error(this.$t("AlreadyTaken"));
        } else {
          this.loading = true;
          let time = this.redObj.create_time;

          //get f + l from text

          let f = this.$route.query.f;
          let l = this.$route.query.l;

          let sig = this.chainApi.sign(id, account, time, f, l);

          this.chainApi
            .getEnvelope(id, account, sig)
            .then(res => {
              console.log(res);
              this.$message.success(this.$t("TranSuccess"));
              this.loading = false;
              localStorage[id] = true;
              let _this = this;
              setTimeout(() => {
                _this.chainApi.getRecord(_this.$route.query.id).then(res => {
                  _this.redObj = res;
                });
              }, 1000);
            })
            .catch(err => {
              console.log(err);
              this.$message.error(this.$t("TranFail"));
              this.loading = false;
              this.chainApi.getRecord(id).then(res => {
                this.redObj = res;
                if (this.redObj.rest_number == 0) {
                  this.$message.error(this.$t("RedOverMessage"));
                }
              });
            });
        }
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@rem: round(1/75 * 2, 6) * 1rem;
.receive_box {
  width: 100%;
  min-height: 100%;
  padding-bottom: 180 * @rem;
  box-sizing: border-box;
  .receive_nav {
    width: 100%;
    height: 44 * @rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15 * @rem;
    background: #ce2344;
    box-sizing: border-box;
    .receive_nav_btn {
      width: 30 * @rem;
      height: 44 * @rem;
      display: flex;
      align-items: center;
      i {
        color: #fff;
        font-size: 25 * @rem;
      }
    }
    .receive_nav_title {
      font-size: 12 * @rem;
      line-height: 44 * @rem;
      color: #fff;
    }
    .receive_nav_text {
      a {
        width: 30 * @rem;
        height: 44 * @rem;
        display: flex;
        align-items: center;
        text-align: right;
        i {
          color: #fff;
          font-size: 23 * @rem;
        }
      }
    }
  }
  .receive_input {
    position: fixed;
    bottom: 0;
    height: 180 * @rem;
    max-width: 375 * @rem;
    width: 100%;
    background-color: #ce2344;
    .account_input {
      display: flex;
      justify-content: center;
      input {
        height: 36 * @rem;
        line-height: 36 * @rem;
        width: calc(100% - 60 * @rem);
        margin-top: 12 * @rem;
        border: 0 none;
        border-radius: 2 * @rem;
        padding: 10 * @rem 15 * @rem;
        font-size: 14 * @rem;
      }
    }
    .input_text {
      text-align: right;
      color: #fff;
      margin-top: 2 * @rem;
      padding-right: 16 * @rem;
      font-size: 12 * @rem;
      line-height: 20 * @rem;
      margin-bottom: 10 * @rem;
    }
  }
}
</style>



